/* Navbar container */
.navbar-class {
  display: flex; /* Use Flexbox for layout */
  align-items: center; /* Center items vertically */
  background-color: #007bff; /* Navbar background color */
  padding: 15px 20px; /* Add padding */
  color: white; /* Text color */
  font-family: Arial, sans-serif; /* Font style */
}

/* Navbar links */
.navbar-class a {
  color: white; /* Link color */
  text-decoration: none; /* Remove underline */
  margin-right: 15px; /* Add spacing between links */
  font-size: 16px; /* Font size */
}

.navbar-class a:hover {
  text-decoration: underline; /* Add underline on hover */
}

/* Logout button container */
.navbar-class-logout-button {
  margin-left: auto;
  display: flex; /* Use Flexbox for alignment */
  align-items: center; /* Center items vertically */
  gap: 10px; /* Add spacing between username and button */
}

/* Logout button */
.navbar-class-logout-button button {
  padding: 8px 12px; /* Add padding */
  font-size: 14px; /* Font size */
  border: none; /* Remove border */
  border-radius: 5px; /* Rounded corners */
  background-color: #ff4d4d; /* Red background for logout button */
  color: white; /* White text */
  cursor: pointer; /* Pointer cursor on hover */
}

.navbar-class-logout-button button:hover {
  background-color: #cc0000; /* Darker red on hover */
}

/* Signed-in username */
.navbar-class-logout-button span {
  font-size: 14px; /* Font size for username */
  font-weight: bold; /* Bold text */
}



.login-container {
  display: flex; /* Use flexbox for alignment */
  flex-direction: column; /* Stack elements vertically */
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  height: 70vh;
  margin: 0px 0; 
}
.login-container input {
  margin: 5px 0;
  width: 30%;
  padding: 5px;
}
.login-container button{
  padding: 10px 12px;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
}
.login-container button:hover {
  opacity: 0.9;
}


/* Compact list styling */
.book-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  background-color: #f9f9f9;
}

.book-info {
  flex: 1;
}

.book-actions {
  display: flex;
  gap: 10px;
}

.book-actions button {
  padding: 5px 10px;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.book-actions button:first-child {
  background-color: #007bff;
  color: white;
}

.book-actions button:last-child {
  background-color: #28a745;
  color: white;
}

.book-actions button:hover {
  opacity: 0.9;
}

/* Expanded book details */
.expanded-book {
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
  margin-top: 20px;
}

.read-books-container {
  display: flex; /* Use flexbox for alignment */
  flex-direction: column; /* Stack elements vertically */
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  margin: 20px 0; /* Add some vertical spacing */
}


/* Parent container: relative positioning lets us absolutely position the filters */
.search-container {
  position: relative;
  width: 90%;
  margin: 20px auto;
}

/* Centered wrapper for search form and feedback text */
.search-form-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Search form: input and button appear in one horizontal row */
.search-form {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Standard input styles */
.search-form input {
  width: 300px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

/* Standard search button styles */
.search-button button {
  padding: 10px 15px;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
}
.search-button button:hover {
  background-color: #0056b3;
}

/* Filters container: absolutely positioned at the top right of the parent */
.filters-container {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  gap: 15px;
  align-items: center;
}

/* Dropdown toggle button styles */
.dropdown-toggle {
  background-color: #007bff;
  color: white;
  padding: 10px 15px;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.dropdown-toggle:hover {
  background-color: #0056b3;
}

/* Dropdown menu styles */
.dropdown-menu {
  position: absolute;
  top: 100%; /* Position the menu below the toggle button */
  left: 0;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  padding: 10px;
  min-width: 150px;
  display: flex;
  flex-direction: column; /* Ensure items are stacked vertically */
  gap: 5px; /* Add spacing between items */
}

/* Dropdown item styles */
.dropdown-item {
  display: flex; /* Align checkbox and label horizontally */
  align-items: center; /* Vertically align checkbox and label */
  gap: 10px; /* Add spacing between checkbox and label */
  cursor: pointer;
}
.dropdown-item:hover {
  background-color: #f0f0f0;
}
.dropdown-item input[type="checkbox"] {
  margin: 0; /* Remove default margin */
  cursor: pointer;
}

/* Label styles */
.dropdown-item label {
  font-size: 14px;
  color: #333;
  cursor: pointer;
}

/* Controls display of the genre dropdown */
.genre-dropdown .dropdown-menu {
  display: none;
}
.genre-dropdown.open .dropdown-menu {
  display: block;
}
.genre-dropdown,
.rating-dropdown {
  position: relative;
  display: inline-block;
}

/* Search results container */
.search-results {
  margin-top: 20px;
}

.error-message p {
  color: red;
  font-size: 14px;
  margin-top: 10px;
}

.query-type-dropdown {
  position: relative;
  display: inline-block;
  margin-bottom: 20px;
}








